function TreeDistributionContent(_OBJ) {

  let obj = _OBJ.content;
  let _content = '<ul type="none" style="margin: 0px;padding:0px;font-size:22px;" >' +
    '<li><\/li>'+
    '<li>' + (obj.id || "") + '<\/li>' +
    '<\/ul>';

  return _content;
}
